/* styles.css */

.topbar-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    word-wrap: wrap;
    padding: 10px 20px;
    background-color: var(--color-9);
    color: white;
    z-index: 1000; /* to ensure it stays on top */
    margin-left: 300px; /* Adjust based on your sidebar's width */
}

.search-container {
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;  /* Adjust the gap to your liking */
}

.topbar-item {
    margin-right: 15px; /* consistent spacing instead of 'gap' */
}

.buttons-container {
    display: flex;
    grid-gap: 10px;
    gap: 10px; /* space between the buttons */
    align-items: center;
    border-radius: 8px;
    background-color: var(--color-7);
}

.topbar-search {
    flex-grow: 1; /* allows this element to take up the remaining space */
    margin: 0 15px;
}

.topbar-tabs {
    display: flex;
    grid-gap: 15px;
    gap: 15px;
}

/* Responsive Design */
@media (max-width: 979px) {
    .topbar-container {
        flex-direction: column;
        margin-left: 64px; /* Adjust based on your sidebar's responsive width */
    }

    .topbar-search {
        width: 100%; /* full width in a vertical layout */
        margin-bottom: 15px; /* space between items in a column */
    }
}

.explorer-container {
